<template>
  <el-card>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="140px"
    >
      <el-form-item label="系统名称" prop="systemName">
        <el-select v-model="ruleForm.systemName" placeholder="请选择系统名称">
          <el-option
            v-for="item in in_system"
            :key="item.code"
            :label="item.name"
            :value="item.code"
            >{{ item.name }}</el-option
          >
        </el-select>
      </el-form-item>
      <el-form-item label="请求方式" prop="type">
        <el-select v-model="ruleForm.type" placeholder="请选择请求方式">
          <el-option
            v-for="item in in_req"
            :key="item.code"
            :label="item.name"
            :value="item.code"
            >{{ item.name }}</el-option
          >
        </el-select>
      </el-form-item>

      <el-form-item label="服务名" prop="service">
        <el-input v-model="ruleForm.service"></el-input>
      </el-form-item>

      <el-form-item label="方法名" prop="method">
        <el-input v-model="ruleForm.method"></el-input>
      </el-form-item>
      <el-form-item label="请求报文" prop="request">
        <el-input v-model="ruleForm.request"></el-input>
      </el-form-item>
      <el-form-item label="返回报文" prop="response">
        <el-input v-model="ruleForm.response"></el-input>
      </el-form-item>
      <el-form-item label="有效标志" prop="flag">
        <el-select v-model="ruleForm.flag" placeholder="请选择有效标志">
          <el-option
            v-for="item in in_type"
            :key="item.code"
            :label="item.name"
            :value="item.code"
            >{{ item.name }}</el-option
          >
        </el-select>
      </el-form-item>
      <el-form-item label="其它参数配置1" prop="bak1">
        <el-input v-model="ruleForm.bak1"></el-input>
      </el-form-item>
      <el-form-item label="其它参数配置2" prop="bak2">
        <el-input v-model="ruleForm.bak2"></el-input>
      </el-form-item>

      <el-form-item style="float: right;">
        <el-button type="primary" @click="submitForm('ruleForm')"
          >提交</el-button
        >
        <el-button type="info" @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      in_system: [],
      in_type: [],
      in_req: [],
      //数据初始化
      ruleForm: {
        systemName: "",
        type: "",
        service: "",
        method: "",
        request: "",
        response: "",
        flag: "",
        bak1: "",
        bak2: "",
      },
      //表单验证规则
      rules: {
        systemName: [
          { required: true, message: "请选择系统名称", trigger: "change" },
        ],
        type: [
          { required: true, message: "请选择活动区域", trigger: "change" },
        ],
        service: [
          { required: true, message: "请输入服务名", trigger: "blur" },
          ],
        method: [{ required: true, message: "请输入方法名", trigger: "blur" },
        ],
        request: [
          { required: true, message: "请输入请求报文", trigger: "blur" },
        ],
        response: [
          { required: true, message: "请输入返回报文", trigger: "blur" },
        ],
        flag: [
          { required: true, message: "请选择有效标志", trigger: "change" },
        ],
        bak1: [
          { required: false, message: "请输入其它参数配置1", trigger: "blur" },
        ],
        bak2: [
          { required: false, message: "请输入其它参数配置2", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    //表单提交
    submitForm(ruleForm) {
      //验证
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          let formData = this.ruleForm;
          this.request.postJson(this.blogapi.add, formData).then((res) => {
            this.$message({
              message: "添加成功！",
              type: "success",
            });
            this.clear();
          });
        } else {
          this.$message({
            message: "添加失败！",
          });
          return false;
        }
      });
    },
    //重置表单
    resetForm(ruleForm) {
      this.$refs[ruleForm].resetFields();
    },
    //重置输入框
    clear: function () {
      this.ruleForm = "";
    },
  },
  //初始化并查找系统名、标志和请求方式
  mounted() {
    let systemQueryParam = {
      dictColumn: "SYSTEM",
    };
    this.request
      .postJson(this.blogapi.dictQuery, systemQueryParam)
      .then((res) => {
        this.in_system = res.data;
      });
    let typeQueryParam = {
      dictColumn: "FLAG",
    };
    this.request
      .postJson(this.blogapi.dictQuery, typeQueryParam)
      .then((res) => {
        this.in_type = res.data;
      });
    let reqQueryParam = {
      dictColumn: "REQUEST",
    };
    this.request.postJson(this.blogapi.dictQuery, reqQueryParam).then((res) => {
      this.in_req = res.data;
    });
  },
};
</script>



