<!--  -->
<template>
  <div class="fileUpload">
    <el-container>
      <el-main>
        <el-row>
          <el-col :span="6">
            <div>上传文档示例:</div>
            <br />
            <!-- <a href="static/Excel/mock1.xls" download="文档示例.xls"> <el-button type="info" round size="small">点击下载</el-button></a> -->

            <a href="../../static/Excel/mock1.xls" download="文档示例.xls">
              <el-button type="info" round size="small">点击下载</el-button></a
            >
          </el-col>
          <el-col :span="8">
            <div class="grid-content">
              <el-upload
                class="upload-demo"
                drag
                :action="url"
                multiple
                accept=".xls,.xlsx"
              >
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">
                  将文件拖到此处，或
                  <em>点击上传</em>
                </div>
                <div class="el-upload__tip" slot="tip">只能上传xls文件</div>
              </el-upload>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content"></div>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  data() {
    //这里存放数据
    return {
      url: this.blogapi.import,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
};
</script>

<style src="./inter.css" scoped></style>



