<template>
    <el-card class="box-card">
      <div>
        <el-row>
          <el-col :md="4">
            <span>系统名称</span>
          </el-col>
          <el-col :md="6">
            <span>{{form.systemname}}</span>
          </el-col>
        </el-row>
        <el-row>
          <el-col :md="4">
            <span>服务名</span>
          </el-col>
          <el-col :md="20">
            <span>{{form.servicename}}</span>
          </el-col>
        </el-row>
        <el-row>
          <el-col :md="4">
            <span>方法名</span>
          </el-col>
          <el-col :md="20">
            <span>{{form.methodname}}</span>
          </el-col>
        </el-row>
        <el-row>
          <el-col :md="4">
            <span>有效标志</span>
          </el-col>
          <el-col :md="6">
            <span>{{form.flag}}</span>
          </el-col>
        </el-row>
        <el-row>
          <el-col :md="4">
            <span>请求时间</span>
          </el-col>
          <el-col :md="20">
            <span>{{form.requesttime}}</span>
          </el-col>
        </el-row>
        <el-row>
          <el-col :md="4">
            <span>请求方法</span>
          </el-col>
          <el-col :md="20">
            <span>{{form.type}}</span>
          </el-col>
        </el-row>

        <el-row>
          <el-col :md="4">
            <span>请求报文</span>
          </el-col>
          <el-col :md="20">
            <span>{{form.request}}</span>
          </el-col>
        </el-row>

        <el-row>
          <el-col :md="4">
            <span>返回报文</span>
          </el-col>
          <el-col :md="20">
            <span>{{form.response}}</span>
          </el-col>
        </el-row>
      </div>
    </el-card>
</template>


<script>
  export default {
    name: "interDetail",
    props: ["params"],
    data() {
      return {
        form: {
          systemname: '',    //系统名称
          servicename: '',    //服务名
          methodname: '',    //方法名
          flag: '',    //有效标志位
          requesttime: '',    //请求时间
          type: '',    //方法类型
          request: '',    //请求报文
          response: ''    //返回报文
        }
      }
    },

    //初始化
    mounted() {
      var param={id:this.params.id};
      this.request.postJson(this.blogapi.detail, param).then(res =>{
      this.form.systemname = res.data.system;
      this.form.servicename = res.data.service;
      this.form.methodname = res.data.method;
      this.form.flag = res.data.flag;
      this.form.requesttime = res.data.opTime;
      this.form.type = res.data.type;
      this.form.request = res.data.request;
      this.form.response = res.data.response;
      });
    },
      watch:{
        params(){
          this.qryInterDetail(this.params.id)
        }
      },
    methods: {

      //查询接口并绑定数据
      qryInterDetail(id) {
        var param={id:id};
            this.request.postJson(this.blogapi.detail, param).then(res =>{
               console.log(res.data);
               this.form.systemname = res.data.system;
               this.form.servicename = res.data.service;
               this.form.methodname = res.data.method;
               this.form.flag = res.data.flag;
               this.form.requesttime = res.data.opTime;
               this.form.type = res.data.type;
               this.form.request = res.data.request;
               this.form.response = res.data.response;
              });
      },
    }
  }
</script>
<style scoped src="./detailandedit.css"></style>


