<template>
  <div class="app">
    <el-row>
      <el-col :span="24">
        <el-main>
          <!-- 搜索部分 -->
          <el-row>
            <el-card shadow="always" class="myborber">
              <el-col :span="6">
                <label for>系统名称</label>
                <el-select v-model="system" clearable placeholder="请选择">
                  <el-option
                    v-for="item in in_system"
                    :key="item.code"
                    :label="item.name"
                    :value="item.code"
                    >{{ item.name }}</el-option
                  >
                </el-select></el-col
              >
              <el-col :span="6">
                <label>服务名</label>
                <el-input
                  class="myinput"
                  v-model="service"
                  placeholder="请输入服务名 "
                ></el-input
              ></el-col>
              <el-col :span="6">
                <label>方法名</label>
                <el-input
                  class="myinput"
                  v-model="method"
                  placeholder="请输入方法名"
                ></el-input>
              </el-col>
              <el-col :span="2">
                <el-button type="primary" @click="clickSearch">搜索 </el-button>
              </el-col>
              <el-col :span="2">
                <el-button type="info" @click="clear" :plain="true"
                  >清空</el-button
                >
              </el-col>
              <br />
            </el-card>
          </el-row>

          <br />

          <!-- 接口列表功能部分 -->
          <el-row style="padding-top: 10px;">
            <el-card shadow="always" class="myborber">
              <el-col :span="18">
                <div class="grid-content bg-purple">
                  <span class="larg-text">接口列表</span>
                </div>
              </el-col>
              <el-col :span="1">
                <el-button
                  type="primary"
                  circle
                  class="circleButton"
                  @click="interAdd"
                  >新增</el-button
                > </el-col
              ><el-col :span="1">
                <el-button
                  type="success"
                  circle
                  class="circleButton"
                  @click="fileUpload"
                  >导入</el-button
                ></el-col
              ><el-col :span="1">
                <el-button circle class="circleButton" @click="interExport"
                  >导出</el-button
                ></el-col
              >
            </el-card>
          </el-row>
          <br />
          <!-- 表格数据部分 -->
          <el-card shadow="always" class="myborber">
            <el-table :data="tableData" style="width: 100%;">
              <!-- <el-table-column prop="id" label="id" width="0" ></el-table-column> -->
              <el-table-column
                type="index"
                :index="indexMethod"
                label="序号" width="50px"
                align="center"
              ></el-table-column>
              <!-- <el-table-column prop="id" label="序号" width="50" ></el-table-column> -->

              <el-table-column prop="system" label="系统名称" width="150px" align="center"></el-table-column>
              <el-table-column
                prop="service"
                label="服务名"
                show-overflow-tooltip
              ></el-table-column>
              <el-table-column
                prop="method"
                label="方法名"
                show-overflow-tooltip
              ></el-table-column>
              <el-table-column
                prop="request"
                label="入参"
                show-overflow-tooltip
              ></el-table-column>
              <el-table-column
                prop="response"
                label="出参"
                show-overflow-tooltip
              ></el-table-column>
              <el-table-column prop="operation" label="操作" width="240">
                <template slot-scope="scope">
                  <el-button
                    type="info"
                    round
                    size="small"
                    @click="detail(scope.row.id)"
                    >查看</el-button
                  >
                  <el-button
                    type="primary"
                    round
                    size="small"
                    @click="edit(scope.row.id)"
                    >编辑</el-button
                  >

                  <el-button
                    type="danger"
                    round
                    size="small"
                    :plain="true"
                    @click="remove(scope.row.id)"
                    >删除</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
            <br />
            <div class="block">
              <span class="demonstration"></span>
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="pageSizes"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="rowCount"
              ></el-pagination>
            </div>
          </el-card>

          <el-dialog
            :title="title"
            :visible.sync="frameShow"
            @close="closeDialog"
          >
            <component :is="frameName" :params="params"></component>
          </el-dialog>
        </el-main>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { postDownload } from "@/common/utils/tools";
import fileUpload from "@/components/inter/fileupload";
import interDetail from "@/components/inter/InterDetail";
import interEdit from "@/components/inter/InterEdit";
import interAdd from "@/components/inter/interAdd";
export default {
  data() {
    return {
      frameShow: false,
      frameName: "",
      title: "",
      homeSrc: "/#/intro",
      tableData: [],
      params: {},
      in_system: [],
      service: "",
      method: "",
      system: "",
      pageSize: 10,
      currentPage: 1,
      pageCount: "",
      rowCount: 0,
      pageSizes: [10, 20, 40, 80],
    };
  },
  methods: {
    // table的序号
    indexMethod(index) {
      return index + 1;
    },

    // 接口删除
    remove: function (val) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          var param = {
            id: val,
          };
          this.request.postJson(this.blogapi.remove, param).then((res) => {
            this.search();
          });
          this.$message({
            type: "success",
            message: "删除成功",
            duration: 1500,
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
             duration: 1000,
          });
        });

      // 接口新增
    },
    interAdd() {
      this.frameShow = true;
      this.frameName = "interAdd";
      this.title = "接口新增";
    },

    // 文件上传
    fileUpload() {
      this.frameShow = true;
      this.frameName = "fileUpload";
      this.title = "文件上传";
    },
    // 查看详情
    detail(val) {
      console.log(val);
      this.frameShow = true;
      this.frameName = "interDetail";
      this.title = "接口详情";
      this.params = {
        id: val,
      };
    },

    // 接口编辑
    edit(val) {
      this.frameShow = true;
      this.frameName = "interEdit";
      this.title = "接口编辑";
      this.params = {
        id: val,
      };
    },
    // dialog关闭时方法
    closeDialog() {
      console.log(this.title);
      var flag = "接口详情";
      if (this.title === flag) {
      } else {
        this.search();
      }
    },

    // 文本框清空
    clear: function () {
      this.service = "";
      this.method = "";
      this.system = "";
      this.$message({
        message: "文本框清除成功",
        type: "success",
        duration: 500,
      });
    },

    clickSearch: function () {
      this.currentPage = 1;
      let param = {
        pageIndex: this.currentPage,
        pageSize: this.pageSize,
        system: this.system,
        service: this.service,
        method: this.method,
      };
      this.request.postJson(this.blogapi.interPages, param).then((res) => {
        this.tableData = [];
        this.tableData = res.data;
        this.pageCount = res.pageInfo.pageCount;
        this.rowCount = res.pageInfo.rowCount;
      });
    },

    //条件组合模糊查询
    search: function () {
      let param = {
        pageIndex: this.currentPage,
        pageSize: this.pageSize,
        system: this.system,
        service: this.service,
        method: this.method,
      };
      this.request.postJson(this.blogapi.interPages, param).then((res) => {
        this.tableData = [];
        this.tableData = res.data;
        this.pageCount = res.pageInfo.pageCount;
        this.rowCount = res.pageInfo.rowCount;
      });
    },

    // 接口导出为Excel文件
    interExport: function () {
      let param = {
        system: this.system,
        service: this.service,
        method: this.method,
      };
      // let param={
      // system:this.system,
      // service:this.service,
      // method:this.method}
      console.log(param);
      console.log(111);
      postDownload(param, this.blogapi.interExport);
    },

    // 每页显示多少条数据改变事件
    handleSizeChange(val) {
      this.pageSize = val;
      this.search();
    },
    // 当前页码改变事件
    handleCurrentChange(val) {
      this.currentPage = val;
      this.search();
    },
  },

  // 初始化事件
  mounted() {
    let Listparam = {
      pageIndex: this.currentPage,
      // pageCount: "",
      // rowCount:"",
      pageSize: this.pageSize,
      system: this.system,
      service: this.service,
      method: this.method,
    };
    this.request.postJson(this.blogapi.interPages, Listparam).then((res) => {
      this.tableData = res.data;
      this.pageCount = res.pageInfo.pageCount;
      this.rowCount = res.pageInfo.rowCount;
    });
    let systemQueryParam = {
      dict_name: "CONFIG",
      dict_column: "SYSTEM",
    };
    this.request
      .postJson(this.blogapi.dictQuery, systemQueryParam)
      .then((res) => {
        this.in_system = res.data;
        /* for(var i=0;i<res.data.length;i++){
                  this.in_system.push(res.data[i].code)
                } */
      });
  },
  components: {
    fileUpload,
    interDetail,
    interEdit,
    interAdd,
  },
};
</script>

<style src="./inter.css" scoped></style>



